@media (max-width: 810px) {
  .CartCoupon {
    width: 100%; } }

@media (min-width: 811px) and (max-width: 1023px) and (-webkit-min-device-pixel-ratio: 1) and (orientation: portrait) {
  .CartCoupon {
    width: 100%; } }

.CartCoupon-Message {
  display: inline-block; }

.CartCoupon-Input {
  display: inline-block; }
  .CartCoupon-Input input {
    width: 200px; }
    @media (max-width: 810px) {
      .CartCoupon-Input input {
        width: 100%; } }
    @media (min-width: 811px) and (max-width: 1023px) and (-webkit-min-device-pixel-ratio: 1) and (orientation: portrait) {
      .CartCoupon-Input input {
        width: 100%; } }
  @media (max-width: 810px) {
    .CartCoupon-Input {
      width: 100%;
      margin-top: 0; } }
  @media (min-width: 811px) and (max-width: 1023px) and (-webkit-min-device-pixel-ratio: 1) and (orientation: portrait) {
    .CartCoupon-Input {
      width: 100%;
      margin-top: 0; } }

.CartCoupon-Button {
  margin-left: 2.4rem; }
  @media (max-width: 810px) {
    .CartCoupon-Button {
      width: 100%;
      margin-left: 0;
      margin-top: 1.2rem; } }
  @media (max-width: 810px) {
    .CartCoupon-Button {
      margin-top: 1.4rem; } }
  @media (min-width: 811px) and (max-width: 1023px) and (-webkit-min-device-pixel-ratio: 1) and (orientation: portrait) {
    .CartCoupon-Button {
      width: 100%;
      margin-left: 0;
      margin-top: 1rem; } }
